.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

._about {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.about__left {
  float: left;
  width: 50%;
  height: 100%;
  padding-top: 150px;
}

.about__left h1 {
  float: right;
  font-size: 5em;
  z-index: 102;
}

.about__left h1, .about__left span {
  display: block;
  text-align: right;
}

.about__left .name {
  font-size: 4em;
}

.about__left .work {
  font-variant: small-caps;
  font-size: .5em;
}

.line {
  position: absolute;
  z-index: -2;
  height: 100%;
  top: 0;
  transform: skew(-22deg);
}

.line1 {
  background: rgba(136, 45, 33, 0.3);
  left: 100%;
  width: 150px;
}

.line2 {
  background: rgba(136, 25, 255, 0.2);
  left: 100%;
  width: 700px;
}

[id^='slide1']:checked + .slide .line1 {
  left: 550px;
  z-index: 100;
  transition: left .65s;
  transition-delay: .3s;
}

[id^='slide1']:checked + .slide .line2 {
  left: -250px;
  transition: left .65s;
  transition-delay: .4s;
}

.contacts {
  width: 100%;
  text-align: center;
  display: block;
  padding-top: 200px;
}

.contacts__item {
  display: inline-block;
}

.contacts__item .fa-stack-2x {
  text-shadow: #673ab7 2px 2px 2px;
}

.contacts__item a {
  color: #eeeeee;
}

.contacts__item a:hover .fa-vk {
  color: #45668e;
}

.contacts__item a:hover .fa-telegram {
  color: #0088cc;
}

.contacts__item a:hover .fa-envelope {
  color: #dd4b39;
}

.contacts__item a:hover .fa-skype {
  color: #00aff0;
}

.contacts__item a:hover .fa-stack-1x {
  transform: scale(1.1);
  text-shadow: #000 2px 2px 2px;
}

[id^='slide4']:checked + .slide .contacts__item--vk {
  animation-delay: .1s;
}

[id^='slide4']:checked + .slide .contacts__item--telegram {
  animation-delay: .2s;
}

[id^='slide4']:checked + .slide .contacts__item--email {
  animation-delay: .3s;
}

[id^='slide4']:checked + .slide .contacts__item--skype {
  animation-delay: .4s;
}

.portfolio {
  overflow: scroll;
  overflow-x: hidden;
  width: 106%;
  margin-left: -2.5%;
  height: 100%;
}

.prt_item {
  animation-delay: .5s;
  width: 19%;
  height: 300px;
  display: block;
  float: left;
  margin: 0.62%;
  box-sizing: border-box;
  overflow: hidden;
}

.prt_item:first-child {
  margin-left: 0;
}

.prt_item:nth-child(5n+1) {
  margin-left: 0;
}

.prt_item:nth-child(5n) {
  margin-right: 0;
}

.hovereffect {
  width: 100%;
  height: 100%;
  float: left;
  overflow: hidden;
  position: relative;
  text-align: center;
  cursor: default;
}

.hovereffect .overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.3);
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.hovereffect .img {
  display: block;
  position: relative;
  -webkit-transition: all .4s linear;
  transition: all .4s linear;
  width: inherit;
  height: inherit;
  background-size: cover;
}

.hovereffect h2 {
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  position: relative;
  font-size: 25px;
  background: rgba(0, 0, 0, 0.6);
  -webkit-transform: translatey(-100px);
  -ms-transform: translatey(-100px);
  transform: translatey(-100px);
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
  padding: 10px;
}

.hovereffect a.info {
  text-decoration: none;
  display: inline-block;
  font-variant: small-caps;
  color: #fff;
  text-shadow: 1px 1px 1px #000;
  width: 100%;
  height: 100%;
  font-size: 24px;
  background-color: transparent;
  opacity: 0;
  filter: alpha(opacity=0);
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
  padding: 157px 14px 14px;
}

.hovereffect:hover .img {
  -ms-transform: scale(1.2);
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}

.hovereffect:hover .overlay {
  opacity: 1;
  filter: alpha(opacity=100);
}

.hovereffect:hover h2, .hovereffect:hover a.info {
  opacity: 1;
  filter: alpha(opacity=100);
  -ms-transform: translatey(0);
  -webkit-transform: translatey(0);
  transform: translatey(0);
}

.hovereffect:hover a.info {
  -webkit-transition-delay: .2s;
  transition-delay: .2s;
}

